@import url(https://fonts.googleapis.com/css2?family=Changa&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Tajawal&display=swap);
.header {
  display: flex;
  align-content: center;
  -webkit-padding-before: 1vh;
          padding-block-start: 1vh;
  -webkit-padding-after: 1vh;
          padding-block-end: 1vh;
  -webkit-padding-start: 6%;
          padding-inline-start: 6%;
  color: var(--primary-text-color);
}

.header:hover {
  color: var(--primary-text-color);
}

.header-item {
  margin: 5px;
}

.header-img {
  width: 50px;
}

.header-text {
  font-size: 2rem;
  font-weight: 600;
  font-family: 'Changa', sans-serif;
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  background-color: var(--primary-background-color);
  font-family: 'Tajawal', sans-serif;
}

.navbar-list {
  display: flex;
  list-style: none;
}

.navbar-list-item-link {
  font-size: 1.1rem;
  color: var(--secondary-text-color);
  display: flex;
  justify-items: center;
  align-content: center;
  -webkit-margin-start: 3.5px;
          margin-inline-start: 3.5px;
  padding: 6px 3.5px;
  cursor: pointer;
}

.navbar-list-item-link-first {
  -webkit-margin-start: 0;
          margin-inline-start: 0;
}

.navbar-list-item-link:hover {
  background-color: var(--secondary-text-color);
  color: var(--primary-text-color);
}

@media (min-width: 660px) {
  .navbar {
    padding: 0 10vw 0 10vw;
  }
  .navbar-list-item-link {
    font-size: 1.4rem;
    padding: 5px;
  }
}

/*
  credit: https://www.w3schools.com/css/css_dropdowns.asp
*/

.dropdown {
  position: relative;
  display: inline-block;
}

.dropdown-content {
  display: none;
  position: absolute;
  background-color: var(--secondary-text-color);
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
  list-style-type: none;
}

.dropdown-content a {
  color: var(--primary-text-color);
  padding: 8px 16px;
  text-decoration: none;
  display: block;
}

/* Change color of dropdown links on hover */
.dropdown-content a:hover {
  color: var(--secondary-text-color);
  background-color: var(--primary-background-color);
}

/* Show the dropdown menu on hover */
.dropdown:hover .dropdown-content {
  display: block;
}

.footer {
  display: flex;
  flex-direction: column;
  width: 100%;
  color: var(--secondary-text-color);
  background-color: var(--primary-background-color);
  text-align: center;
  margin-top: 30px;
  padding: 7.5px;
}

.footer-element {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.footer-link {
  margin: 0 5px;
}

.footer-link:hover {
  color: cornflowerblue;
}

.books-container-title {
  margin-top: 20px;
  font-size: 1.4rem;
  align-self: flex-start;
  -webkit-margin-start: 5%;
          margin-inline-start: 5%;
  color: var(--primary-text-color);
}

.books-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}

.book {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  margin: 7.5px;
  padding: 7.5px;
  width: 150px;
  opacity: 0.88;
  transition-timing-function: ease-out;
  transition: opacity 0.5s;
}

.book:hover {
  opacity: 1;
  outline: 2px solid #eb8f8f;
}

.book-link {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.book-cover {
  width: 120px;
  height: 180px;
  padding: 5px;
  border-bottom: 1px solid #eb8f8f;
}

.book-info {
  margin-top: 2px;
  font-size: 1rem;
  font-weight: 600;
  color: var(--primary-background-color);
}

.book-author {
  font-weight: 500;
  font-style: italic;
}

.no-books {
  font-size: 1.4rem;
  margin: 20px;
  text-align: center;
  color: var(--lighter-background-color);
}

@media (min-width: 660px) {
  .books-container-title {
    -webkit-margin-start: 15%;
            margin-inline-start: 15%;
    font-size: 1.8rem;
  }
  .book {
    width: 250px;
  }
  .book-cover {
    width: 180px;
    height: 270px;
  }
  .book-info {
    font-size: 1.3rem;
  }
  .no-books {
    font-size: 1.8rem;
  }
}

/*
  credit: https://codepen.io/mihaeltomic/pen/vmwMdm
*/
.search-form {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 75%;
  margin: 0 auto;
}

.search-input {
  width: 100%;
  padding: 6px 12px;
  font-size: 1.1rem;
  background-color: transparent;
  transition: transform 250ms ease-in-out;
  color: sienna;
  border-radius: 50px;
  border: 1px solid #b30000;
  transition: all 250ms ease-in-out;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  transform-style: preserve-3d;
  margin: 3px;
}

.search-input::-webkit-input-placeholder {
  color: sienna;
}

.search-input:-ms-input-placeholder {
  color: sienna;
}

.search-input::-ms-input-placeholder {
  color: sienna;
}

.search-input::placeholder {
  color: sienna;
}

.search-input:hover,
.search-input:focus {
  padding: 6px 6px;
  outline: 0;
  border: 1px solid transparent;
  border-bottom: 1px solid #b30000;
  border-radius: 0;
}

.search-submit {
  width: 5%;
  cursor: pointer;
  outline: none;
  border: none;
  background-color: transparent;
  background-image: url(data:image/png;base64,);
  background-repeat: no-repeat;
  background-size: 18px 18px;
}

@media (min-width: 660px) {
  .search-form {
    max-width: 70vh;
    margin: 0 auto;
  }
  .search-input {
    font-size: 1.5rem;
  }
}

.banner-container {
  width: 100%;
  padding: 15px 0;
  display: flex;
  justify-content: center;
}

.banner-image {
