}

.paragraph-head {
  margin-top: 15px;
  padding-right: 10px;
  font-size: 1.6rem;
}

.paragraph {
  padding-right: 15px;
  font-size: 1.4rem;
}

.paragraph-list {
  margin-right: 15px;
}

.paragraph-list-item {
  padding-right: 5px;
  font-size: 1.3rem;
}
@media (min-width: 660px) {
  .static-page {
    max-width: 60%;
  }
}

.loading-full-page {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 150px;
  margin: 30vh auto;
}

.container {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

@media (min-width: 660px) {
  .container {
    width: 85%;
    margin: 0 auto;
  }
}

