:root {
  --primary-text-color: sienna;
  --secondary-text-color: #eee;
  --primary-background-color: maroon;
  --lighter-background-color: #b30000;
  --currency: ' $';
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Tajawal', sans-serif;
  font-size: 14px;
}

head,
body {
  width: 100%;
  height: 100%;
}

#root {
  min-height: 100vh;
  display: grid;
  grid-template-rows: auto auto 1fr auto;
}

a,
a:hover {
  text-decoration: none;
  color: inherit;
}

