.footer {
  display: flex;
  flex-direction: column;
  width: 100%;
  color: var(--secondary-text-color);
  background-color: var(--primary-background-color);
  text-align: center;
  margin-top: 30px;
  padding: 7.5px;
}

.footer-element {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.footer-link {
  margin: 0 5px;
}

.footer-link:hover {
  color: cornflowerblue;
}
